<template>
  <div>
    <div class="oper-div">
      <el-input placeholder="请输入专业" v-model="deptInput" clearable="">
        <template #prepend>专业：</template>
        <template #append>
          <el-button @click="getDeptRank">
            <el-icon><Search /></el-icon>
          </el-button>
        </template>
      </el-input>
    </div>
    <el-card shadow="never">
      <div class="oper-div">
        <el-button type="primary" plain style="margin-right: 10px;" @click="exportData">
          <el-icon><Download /></el-icon>
          <span>导出</span>
        </el-button>
      </div>
      <el-divider content-position="left">学生成绩排名</el-divider>
      <el-table ref="multipleTable" :data="deptRankData" tooltip-effect="dark" style="width: 100%;" border stripe height="calc(100vh - 250px)">
        <el-table-column prop="Sno" label="学号" show-overflow-tooltip></el-table-column>
        <el-table-column prop="Sname" label="学生姓名" show-overflow-tooltip></el-table-column>
        <el-table-column prop="Cno" label="课程号" show-overflow-tooltip></el-table-column>
        <el-table-column prop="Cname" label="课程名" show-overflow-tooltip></el-table-column>
        <el-table-column prop="Grade" label="成绩" show-overflow-tooltip></el-table-column>
      </el-table>
    </el-card>

  </div>
</template>

<script>
import Request from '../../tools/Request'
import Tools from '../../tools/Tools'

export default {
  // eslint-disable-next-line
  name: 'Rank',
  data () {
    return {
      deptRankData: [],
      deptInput: '',
      currentPage: 1,
      pageSize: 5,
      totalRows: 0
    }
  },
  methods: {
    exportData(){
      Tools.exportJson("成绩排名.json", JSON.stringify(this.deptRankData));
    },
    getDeptRank () {
      Request
        .get('app/deptrank', {
          params: {
            dept: this.deptInput
          }
        })
        .then((res) => {
          this.deptRankData = []
          if (res.code === 0) {
            if (res.rows === 0) {
              this.$message({
                message: '未找到该专业的信息',
                type: 'warning'
              })
            } else {
              this.deptRankData = res.data
              this.$message({
                message: '查找专业成功',
                type: 'success',
                showClose: true
              })
            }
          } else {
            this.$message.error({
              message: '查找专业失败，错误信息：' + res.error,
              showClose: true
            })
          }
        })
        .catch((err) => {
          this.$message.error({
            message: '获取信息失败，错误信息：' + err,
            showClose: true
          })
        })
    },
    handleSizeChange (val) {
      this.pageSize = val
      this.getTable()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getTable()
    }
  }
}
</script>

<style scoped>
.oper-div {
  margin-bottom: 10px;
  display: flex;
}
</style>
