import { createRouter, createWebHashHistory} from 'vue-router'
import Login from '../components/login/Login.vue'
import Home from '../components/home/Home.vue'
import Student from '../components/tables/Student.vue'
import Course from '../components/tables/Course.vue'
import SC from '../components/tables/SC.vue'
import Search from '../components/tables/Search.vue'
import Rank from '../components/tables/Rank.vue'
import Grade from '../components/tables/Grade.vue'
import Store from '../tools/Storage'

const routes = [
    {
        path: '/login',
        component: Login,
        name: "login"
    },
    {
        path: '/home',
        component: Home,
        name: "home",
        redirect: '/home/student',
        children:[
            {
                path: 'student',
                component: Student,
                name: 'student'
            }, 
            {
                path: 'course',
                component: Course,
                name: 'course'
            }, 
            {
                path: 'sc',
                component: SC,
                name: 'sc'
            }, 
            {
                path: 'grade',
                component: Grade,
                name: 'grade'
            }, 
            {
                path: 'rank',
                component: Rank,
                name: 'rank'
            }, 
            {
                path: 'search',
                component: Search,
                name: 'search'
            }
        ]
    } 
]

const Router = createRouter({
    history: createWebHashHistory(),
    routes: routes
})

Router.beforeEach((from) => {
    let isLogin = Store.getters.isLogin;
    if (isLogin || from.name == 'login') {
        return true;
    } else {
        return {name: 'login'}
    }
})

export default Router
